import React, { useEffect, useMemo } from "react"
import useKakaoEvent from "../hooks/useKakaoEvent"
import useMap from "../hooks/useMap"

export interface CircleProps {
  /**
   *   .
   */
  center: {
    lat: number
    lng: number
  }
  /**
   *     ( ).
   */
  radius: number

  /**
   * #xxxxxx    (: #F10000)
   */
  fillColor?: string
  /**
   *   (0-1) (: 0)
   */
  fillOpacity?: number
  /**
   *     (: 3)
   */
  strokeWeight?: number
  /**
   * #xxxxxx    (: #F10000)
   */
  strokeColor?: string
  /**
   *   (0-1) (: 0.6)
   */
  strokeOpacity?: number
  /**
   *   (: solid)
   */
  strokeStyle?: kakao.maps.StrokeStyles
  /**
   *  z-index  
   */
  zIndex?: number

  /**
   *     .
   */
  onMouseover?: (
    target: kakao.maps.Circle,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *     .
   */
  onMouseout?: (
    target: kakao.maps.Circle,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *    .
   */
  onMousemove?: (
    target: kakao.maps.Circle,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *     .
   */
  onMousedown?: (
    target: kakao.maps.Circle,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *   .
   */
  onClick?: (
    target: kakao.maps.Circle,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void

  /**
   *    .
   */
  onCreate?: (target: kakao.maps.Circle) => void
}

/**
 * Map   .
 */
const Circle: React.FC<CircleProps> = ({
  center,
  radius,
  fillColor,
  fillOpacity,
  strokeColor,
  strokeOpacity,
  strokeStyle,
  strokeWeight,
  zIndex,
  onMouseover,
  onMouseout,
  onMousemove,
  onMousedown,
  onClick,
  onCreate,
}) => {
  const map = useMap(`Circle`)

  const circleCenter = useMemo(() => {
    return new kakao.maps.LatLng(center.lat, center.lng)
  }, [center.lat, center.lng])

  const circle = useMemo(() => {
    return new kakao.maps.Circle({
      center: circleCenter,
      radius,
      fillColor,
      fillOpacity,
      strokeColor,
      strokeOpacity,
      strokeStyle,
      strokeWeight,
      zIndex,
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    circle.setMap(map)
    return () => {
      circle.setMap(null)
    }
  }, [map, circle])

  useEffect(() => {
    if (onCreate) onCreate(circle)
  }, [circle, onCreate])

  useEffect(() => {
    if (circle) circle.setPosition(circleCenter)
  }, [circle, circleCenter])

  useEffect(() => {
    circle.setRadius(radius)
  }, [circle, radius])

  useEffect(() => {
    if (!zIndex) return
    circle.setZIndex(zIndex)
  }, [circle, zIndex])

  useEffect(() => {
    circle.setOptions({
      fillColor,
      fillOpacity,
      strokeColor,
      strokeOpacity,
      strokeStyle,
      strokeWeight,
    })
  }, [
    circle,
    fillColor,
    fillOpacity,
    strokeColor,
    strokeOpacity,
    strokeStyle,
    strokeWeight,
  ])

  useKakaoEvent(circle, "mouseover", onMouseover)
  useKakaoEvent(circle, "mouseout", onMouseout)
  useKakaoEvent(circle, "mousemove", onMousemove)
  useKakaoEvent(circle, "mousedown", onMousedown)
  useKakaoEvent(circle, "click", onClick)

  return null
}

export default Circle
